'use client';

import { useFetchInterviewById } from "@/hooks/useFetchInterviewById";
import { useFetchInterviews } from "@/hooks/useFetchInterviews";
import { useEffect } from "react";

export default function InterviewCard({interview_id} : {interview_id: number}) {
    const id = interview_id;

    const {interview} = useFetchInterviewById(id);

    if (!interview) return <div>not found</div>

    return (
        <div className="flex mt-10 items-center border border-gray-200 text-black">
            {interview.title}
        </div>
    )

}